<template>
  <div :class="style.title">
    <ul>
      <li>{{locationCity}} {{locationWeather}}</li>
      <li>{{nowTime}}</li>
    </ul>
    <p></p>
    <div>
      <el-dropdown v-if="userName" @command="handleCommand">
        <span class="el-dropdown-link">{{userName}}</span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command="pwd"></el-dropdown-item>
          <el-dropdown-item command="exit"></el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <img :class="style.imgtitle" src="/static/assets/whitepeople.gif" v-if="userName">
      <span v-else @click="loginPlease"></span>
    </div>
  </div>
</template>

<script>
import style from "@/css/main";
import BMap from "BMap";
import { getWeatherata } from "./mutation-types";
import { mapActions } from "vuex";
export default {
  data() {
    return {
      locationCity: "",
      locationWeather:"",
      nowTime: "",
      style,
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      },
      value: "",
      userName: "",
      isLogin: false
    };
  },
  methods: {
    //
    ...mapActions({
      getWeatherata
    }),
    //
    loginPlease(){
       this.$emit("login");
    },
    handleCommand(command) {
      if (command == "exit") {
        this.userName = "";
        localStorage.setItem("user", "");
        location.reload();
      }
    },
    // 
    timeFormate(timeStamp) {
      let year = new Date(timeStamp).getFullYear();
      let month =
        new Date(timeStamp).getMonth() + 1 < 10
          ? "0" + (new Date(timeStamp).getMonth() + 1)
          : new Date(timeStamp).getMonth() + 1;
      let date =
        new Date(timeStamp).getDate() < 10
          ? "0" + new Date(timeStamp).getDate()
          : new Date(timeStamp).getDate();
      let hh =
        new Date(timeStamp).getHours() < 10
          ? "0" + new Date(timeStamp).getHours()
          : new Date(timeStamp).getHours();
      let mm =
        new Date(timeStamp).getMinutes() < 10
          ? "0" + new Date(timeStamp).getMinutes()
          : new Date(timeStamp).getMinutes();
      // let ss =new Date(timeStamp).getSeconds() < 10? "0" + new Date(timeStamp).getSeconds(): new Date(timeStamp).getSeconds();
      // return year + "" + month + "" + date +""+" "+hh+":"+mm ;
      this.nowTime =
        year + "" + month + "" + date + "" + " " + hh + ":" + mm;
      // console.log(this.nowTime);
    },
    // 
    nowTimes() {
      this.timeFormate(new Date());
      setInterval(this.nowTimes, 30 * 1000);
    },
    //
    dz() {
      var that = this;
      var geolocation = new BMap.Geolocation();
      geolocation.getCurrentPosition(
        function getinfo(position) {
          let city = position.address.city; //
          let province = position.address.province;
          // console.log(city+'--'+province)
          that.locationCity = city; //locationCity
          that.getWeather();
        },
        function(e) {
          that.locationCity = "";
        },
        {
          provider: "baidu"
        }
      );
    },
    async getWeather() {
      // 
      let result = await this.getWeatherata({
        city: this.locationCity
      });
      if (!result) return;
      let data = JSON.parse(result);
      if(data.desc=='OK'){
        this.locationWeather = data.data.forecast[0].type;
      }else{
        this.locationWeather = '';
      }
    },
  },
  mounted() {
    //
    this.nowTimes();
    //
    this.dz();
    if (localStorage.getItem("user")) {
      this.userName = JSON.parse(localStorage.getItem("user")).userName;
    }
  }
};
</script>

<style>
</style>



